.footerSection {
    position: fixed;
    left: 0;
    bottom: 0;
    margin-top: 5px;
    width: 100%;
    background-color: rgb(88, 90, 224);
    opacity: .9;
    color: white;
    text-align: center;
    overflow: hidden;
}
.email {
    position: fixed;
    left: 1%;
    color: white;
}
.linkedin {
    position: fixed;
    right: 1%;
    color: white;
}
.linkedin:hover {
    color: red;
    text-decoration: none;
}
.email:hover {
    color: red;
    text-decoration: none;
}
.resume:hover {
    color: red;
    text-decoration: none;
}
