.resume {
    position: fixed;
    left: 75%;
    color: white;
}
@keyframes rainbow {
    from {
        color: #2828f0;
      }
      10% {
        color: #0099ff;
      }
      50% {
        color: #00ff00;
      }
      75% {
        color: #ff002b;
      }
      100% {
        color: #fac124;
      }
      0%,100% {
        background-position: 0 0;
    }
        50% {
        background-position: 100% 0;
    }
}